@import url(https://fonts.googleapis.com/css2?family=Recursive:wght@300;400;500;700&family=Righteous&family=Roboto:wght@100;300;400;700&display=swap);
.welcome-container{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.welcome-container img{
    width: 110px;
    margin-bottom: 0;
}

.welcome-container h1 {
    font-family: 'Righteous';
    font-weight: 400;
    font-size: 56px;
    line-height: 62px;
    color: #fff;
    margin-bottom: 140px;
}
input{
    width: 282px;
    height: 50px;
    border-radius: 10px;
    margin-bottom: 10px;
    border: 2px solid #D70900;

    outline: none;

    font-family: 'Recursive';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    color: #000
}

.deck-description{
    font-family: 'Righteous';
    font-weight: 400;
    font-size: 24px;
    color: #fff;
    margin-bottom: 10px;
}
button {
    width: 282px;
    height: 62px;
    background: #FFFFFF;
    border: 2px solid #D70900;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    font-family: 'Recursive';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    color: #D70900;
}

button:disabled{
    background: #E8E8E8;
    border: none;
    color: #C0C0C0;

}
select {
    width: 282px;
    height: 50px;
    background: #FFFFFF;
    border-radius: 10px;
    margin-bottom: 10px;
    border: 2px solid #D70900;

    outline: none;

    font-family: 'Recursive';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    color: #000
}

option[value=""][disabled] {
    display: none;
}

.contador {
    display: flex;
    margin: 10px auto;
    border-radius: 10px;
    border: 2px solid #D70900;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
}
.decrementar {
    border-radius: 10px 0px 0px 10px;
    height: 50px;
    width: 40px;
    font-weight: bold;
    font-size: 24px;
    line-height: 28px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    letter-spacing: 0.018em;
    cursor: pointer;
    background-color: #fff;
    color: #D70900;
    border-right: 2px solid #D70900;
}
.incrementar {
    border-radius: 0px 10px 10px 0px;
    height: 50px;
    width: 40px;
    font-weight: bold;
    font-size: 24px;
    line-height: 28px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    letter-spacing: 0.018em;
    cursor: pointer;
    background-color: #fff;
    color: #D70900;
    border-left: 2px solid #D70900;
}

.valor {
    width: 202px;
    height: 50px;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
    background-color: #fff;
    color: #D70900;
    display: flex;
    justify-content: center;
    align-items: center;
}
.card-escondido{
    width: calc(100vw - 40px) ;
    max-width: 680px;
    height: 80px;
    background: #FFFFFF;
    box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    display: flex;
    justify-content: space-between;	
    align-items: center;
    margin:10px auto;
}


.card-escondido p{
    font-family: 'Recursive';
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    margin-left: 20px;
}

.card-escondido ion-icon{
    font-size:24px;
    margin-right: 24px;
}

.naoLembro * {
    color: #FF3030;
    text-decoration: line-through;
    font-size: 24px;
}

.quaseNaoLembrei * {
    color: #FF922E;
    -webkit-text-decoration-line: line-through;
            text-decoration-line: line-through;
    font-size: 24px;
}

.zap * {
    color: #2FBE34;
    -webkit-text-decoration-line: line-through;
            text-decoration-line: line-through;
    font-size: 24px;
}

.card {
    width: calc(100vw - 40px) ;
    max-width: 680px;
    height: clamp(200px, calc(268px - 10vw) , 230px);
    position: relative;
    margin: 10px auto;
  }

.face {
    width: 100%;
    height: 100%;
    background: #FFFFD5;
    box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    display: flex;
    justify-content: space-between;	
    align-items: flex-start;
    padding: 16px;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
    transition: all 1s;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}

.card-resposta {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
  }

.card.virado .card-resposta {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
}

.card.virado .card-pergunta {
    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg);
}

.card-pergunta p, .card-resposta p{
    font-family: 'Recursive';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    
    color: #000;
}

.card-pergunta #setinha{
    width:40px;
    position: absolute;
    right: 10px;
    bottom: 10px;
}

.card-resposta {
    flex-direction: column;
}

.opcoes {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items:flex-end;
}

.opcao {
    min-width: 100px;
    width: calc(calc(100% / 3) - 10px);
    height: 48px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.opcao p{
    text-align: center;
    font-family: 'Recursive';
    font-weight: 400;
    font-size: 14px;
    line-height:16px;
    color: #FFFFFF;
}

.red {
    background: #FF3030;
}

.orange {
    background: #FF922E;
}

.green {
    background: #2FBE34;
}
.footer-container {
    background: #FFFFFF;
    box-shadow: 0px -4px 6px rgba(0, 0, 0, 0.05);
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    min-height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.footer-container Button{
    margin-bottom: 20px;
    margin-top: 10px;
}

.footer-container>p{
    font-family: 'Recursive';
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;

    color: #333333;
}

.naoLembro * {
    color: #FF3030;
    text-decoration: line-through;
    font-size: 24px;
}

.quaseNaoLembrei * {
    color: #FF922E;
    -webkit-text-decoration-line: line-through;
            text-decoration-line: line-through;
    font-size: 24px;
}

.zap * {
    color: #2FBE34;
    -webkit-text-decoration-line: line-through;
            text-decoration-line: line-through;
    font-size: 24px;
}

.sequencia{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.congrats {
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.congrats>div {
    display: flex;
    justify-content: center;
    align-items: center;
}

.congrats img {
    width: 36px;
    margin-right: 10px;
}

.congrats>div>p {
    font-family: 'Recursive';
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;

    color: #333333;
}

.congrats>p {
    margin-top: 20px;
    font-family: 'Recursive';
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
    
    color: #333333;
}

.header-container {
    display: flex;
    justify-content: center;
    margin-top: 40px;
    margin-bottom: 40px;
}

.header-container img {
    width: 52px;
    margin-right: 20px;
}

.header-container h2 {
    font-family: 'Righteous';
    font-style: normal;
    font-weight: 400;
    font-size: 40px;
    line-height: 50px;
    display: flex;
    align-items: center;
    text-align: center;
    color: #FFFFFF;
}
.GameScreen-container {
    padding-bottom: 300px;
}
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

* {
	box-sizing: border-box;
}
body{
    font-family: 'Roboto';
    background-color: #FB6B6B;
}

